.technologiesPage{
    margin: 3% 0 0 8%;
    display: grid;
    width: 50vw;
    height: 60vh;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
    align-items: center;
    justify-items: center;
    gap: 50px;
}

.icon{
    width: 100%;
    opacity: 0;
    transition: 3s;
}

.showIcon{
    opacity: 1;
}

@media screen and (max-width: 1400px){
    .technologiesPage{
        margin: 3% 0 0 10%;
    }
}

@media screen and (max-width: 1320px){
    .technologiesPage{
        margin: 3% 0 0 12%;
    }
}

@media screen and (max-width: 1220px){
    .technologiesPage{
        margin: 3% 0 0 12%;
        width: 63vw;
    }
}

@media screen and (max-width: 1120px){
    .technologiesPage{
        margin: 3% 0 0 16%;
    }
}

@media screen and (max-width: 1020px){
    .technologiesPage{
        margin: 3% auto 0 auto;
        width: 67vw;
    }
}

@media screen and (max-width: 920px){
    .technologiesPage{
        margin: 3% auto 0 auto;
        width: 70vw;
    }
}

@media screen and (max-width: 820px){
    .technologiesPage{
        margin: 3% auto 0 auto;
        width: 90vw;
        gap: 20px
    }
}

@media screen and (max-width: 580px){
    .technologiesPage{
        margin: 3% auto 0 auto;
        display: grid;
        width: 80vw;
        height: 60vh;
        grid-template-columns: 1fr 1fr 1fr;
        grid-template-rows: 1fr 1fr 1fr 1fr;
        align-items: center;
        justify-items: center;
        gap: 20px;
    }
}