.ContactPageHeader{
    color:white;
    font-size: 62px;
    font-family: 'Audiowide';
    padding:  150px 0 0 12%;
    font-weight: 400;
}

.landingPageSubHeader{
    color: white;
    font-size: 30px;
    font-weight: 300;
    padding: 8px 0 0 12%;
    font-family: 'Abel', sans-serif;
}

.introContainer{
    width: 40%;
    margin: 10vh 0 0 12%;
    display: flex;
    justify-content: start;
}

#contactContainSmall{
    margin: 3vh 0 0 12%;
}

#atIcon{
    grid-area: theIcon;
    width: fit-content;
    color: rgb(0, 203, 253);
    font-size: 40px;
}

#school{
    grid-area: university;
    padding:0 0 0 3%;
}

.landingPageDescription{
    width: 40%;
    margin: 1vh 0 0 12%;
    color: white;
}

#resume{
    padding: 0;
    margin: 1.5vh 0 0 12%;
    color: rgb(0, 203, 253);
    font-family: 'Audiowide';
    font-weight: 400;
}

#firstProject{
    padding: 8% 0 0 5%;
}

.githubIcon{
    width: 40px;
}

@media screen and (max-width: 580px){
    .landingPageDescription{
        width: 85%;
        margin: 1vh auto 0 auto;
        color: white;
        font-size: 16px;
    }

    .introContainer{
        width: 80%;
        margin: 6vh auto 0 5%;
    }

    #resume{
        margin: 1.5vh 0 30% 7%;
        color: rgb(0, 203, 253);
        font-family: 'Audiowide';
        font-weight: 400;
    }

    .landingPageSubHeader{
        font-size: 20px;
        padding: 8px 0 0 7%;
    }

    .ContactPageHeader{
        font-size: 42px;
        padding:  100px 0 0 5%;
    }

    .landingPageSubHeader{
        padding: 8px 0 0 6%;
    }

    #contactContainSmall{
        margin: 3vh 0 0 6%;

    }
}