.project{
    width: 50vw;
    height: 18vh;
    display: grid;
    top: 20%;
    padding: 4% 0 0 5%;
    grid-template-columns: 1fr 2.2fr;
    grid-template-rows: 1fr 3fr;
    grid-template-areas:"image title"
                         "image description";
    align-items: center;
    transform: translateX(-100%);
    transition: transform 1s ease-in;
}

.landingPageHeaderType2{
    color:white;
    font-size: 62px;
    font-family: 'Audiowide';
    padding: 20px 0 0 7%;
    font-weight: 400;
}

.projectTitle{
    margin: 0 0 0 4%;
    grid-area: title;
    padding: 0 0 0 0;
    color:rgb(0, 203, 253);
}

.githubLink{
    color:white;
}

.projectimg{
    height: 100%;
    width: 100%;
    grid-area: image;
    object-fit: cover;
    border: 1px solid white;
}

.projectDescription{
    grid-area: description;
    margin: 8px 0 0 4%;
    color:white;
    align-self:start;
}

.prog1{
    transform: translateX(-100%);
    transition: transform 2s ease-in;
}

.progSlide{
    transform: translateX(0);
}

@media screen and (max-width: 1430px){
    .project{
        width: 60vw;
    }
}


@media screen and (max-width: 1200px){
    .project{
        width: 70vw;
    }
}

@media screen and (max-width: 1024px){
    .project{
        width: 80vw;
    }

    .landingPageHeaderType2{
        font-size: 52px;
        text-align: center;
        padding: 20px 0 0 0;
    }
}

@media screen and (max-width: 900px){
    .project{
        width: 90vw;
    }
}

@media screen and (max-width: 810px){
    .project{
        width: 90vw;
        height: auto;
        display: grid;
        top: 20%;
        padding: 8% 0 0 5%;
        grid-template-rows: 3fr 1fr 1fr;
        grid-template-columns: 1fr;
        grid-template-areas: "image"
                             "title"
                             "description";
        align-items: center;
    }

    .projectimg{
        height: 95%;
        width: 80%;
        grid-area: image;
        object-fit: cover;
        border: 1px solid white;
        margin: 0 auto 0 auto;
    }

    .projectDescription{
        margin: 0px auto 0 auto;
        text-align: center;
    }

    .projectTitle{
        margin: 0 auto 0 auto;
    }
}

@media screen and (max-width: 710px){

    .landingPageHeaderType2{
        font-size: 42px;
    }
}

@media screen and (max-width: 580px){

    .landingPageHeaderType2{
        font-size:27px;
    }
    .project{
        width: 90vw;
        height: auto;
        display: grid;
        top: 20%;
        padding: 8% 0 0 5%;
        grid-template-rows: 2fr auto auto;
        grid-template-columns: 1fr;
        grid-template-areas: "image"
                             "title"
                             "description";
        align-items: center;
    }

    .projectimg{
        height: 100%;
        width: 80%;
        grid-area: image;
        object-fit: cover;
        border: 1px solid white;
        margin: 0 auto 0 auto;
    }

    .projectDescription{
        margin: 0px auto 0 auto;
        text-align: center;
    }

    .projectTitle{
        margin: 15px auto 10px auto;
        font-size: 18px;
    }
}
